import React from 'react';

interface ColorfulBackgroundProps {
  pageType: 'home' | 'services' | 'locations' | 'about' | 'contact' | 'faq' | 'areas' | 'problems' | 'surfaces';
  children: React.ReactNode;
}

export function ColorfulBackground({ pageType, children }: ColorfulBackgroundProps) {
  const getBackgroundStyle = () => {
    switch (pageType) {
      case 'home':
        return {
          background: `linear-gradient(135deg, 
            #667eea 0%, 
            #764ba2 25%, 
            #f093fb 50%, 
            #f5576c 75%, 
            #4facfe 100%)`,
          backgroundSize: '400% 400%',
          animation: 'gradientShift 15s ease infinite'
        };
      case 'services':
        return {
          background: `linear-gradient(135deg, 
            #ff9a9e 0%, 
            #fecfef 25%, 
            #fecfef 50%, 
            #a8edea 75%, 
            #8ec5fc 100%)`,
          backgroundSize: '400% 400%',
          animation: 'gradientShift 12s ease infinite'
        };
      case 'locations':
        return {
          background: `linear-gradient(135deg, 
            #a18cd1 0%, 
            #fbc2eb 25%, 
            #ffecd2 50%, 
            #fcb69f 75%, 
            #ff9a9e 100%)`,
          backgroundSize: '400% 400%',
          animation: 'gradientShift 18s ease infinite'
        };
      case 'about':
        return {
          background: `linear-gradient(135deg, 
            #667eea 0%, 
            #764ba2 25%, 
            #667eea 50%, 
            #764ba2 75%, 
            #667eea 100%)`,
          backgroundSize: '400% 400%',
          animation: 'gradientShift 20s ease infinite'
        };
      case 'contact':
        return {
          background: `linear-gradient(135deg, 
            #ffecd2 0%, 
            #fcb69f 25%, 
            #ff9a9e 50%, 
            #fecfef 75%, 
            #a8edea 100%)`,
          backgroundSize: '400% 400%',
          animation: 'gradientShift 10s ease infinite'
        };
      case 'faq':
        return {
          background: `linear-gradient(135deg, 
            #8ec5fc 0%, 
            #e0c3fc 25%, 
            #8ec5fc 50%, 
            #e0c3fc 75%, 
            #8ec5fc 100%)`,
          backgroundSize: '400% 400%',
          animation: 'gradientShift 16s ease infinite'
        };
      case 'areas':
        return {
          background: `linear-gradient(135deg, 
            #f093fb 0%, 
            #f5576c 25%, 
            #4facfe 50%, 
            #00f2fe 75%, 
            #4facfe 100%)`,
          backgroundSize: '400% 400%',
          animation: 'gradientShift 14s ease infinite'
        };
      case 'problems':
        return {
          background: `linear-gradient(135deg, 
            #f5576c 0%, 
            #f093fb 25%, 
            #f5576c 50%, 
            #f093fb 75%, 
            #f5576c 100%)`,
          backgroundSize: '400% 400%',
          animation: 'gradientShift 22s ease infinite'
        };
      case 'surfaces':
        return {
          background: `linear-gradient(135deg, 
            #00f2fe 0%, 
            #4facfe 25%, 
            #00f2fe 50%, 
            #4facfe 75%, 
            #00f2fe 100%)`,
          backgroundSize: '400% 400%',
          animation: 'gradientShift 13s ease infinite'
        };
      default:
        return {
          background: `linear-gradient(135deg, 
            #667eea 0%, 
            #764ba2 100%)`
        };
    }
  };

  return (
    <div 
      className="min-h-screen relative overflow-hidden"
      style={getBackgroundStyle()}
    >
      {/* Animated floating elements */}
      <div className="absolute inset-0 overflow-hidden">
        {[...Array(20)].map((_, i) => (
          <div
            key={i}
            className="absolute rounded-full opacity-20"
            style={{
              width: `${Math.random() * 100 + 20}px`,
              height: `${Math.random() * 100 + 20}px`,
              backgroundColor: `hsl(${Math.random() * 360}, 70%, 60%)`,
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
              animation: `float ${Math.random() * 10 + 10}s ease-in-out infinite`,
              animationDelay: `${Math.random() * 5}s`
            }}
          />
        ))}
      </div>
      
      {/* Content overlay */}
      <div className="relative z-10">
        {children}
      </div>
      

    </div>
  );
}